<template>
  <div>
    <el-container class="login-container">
      <el-header class="login-header">
        <div class="login-logo-title">
          <img :src="logo" v-if="!hiddenLogo" />
          <span>{{ title }}</span>
        </div>
      </el-header>
      <el-main class="login-main">
        <div class="content-container">
          <div class="login-scene">
            <img :src="scene" />
          </div>
          <el-form
            ref="loginForm"
            :model="loginForm"
            :rules="loginRules"
            class="login-form"
            auto-complete="on"
            label-position="left"
          >
            <div class="title-container">
              <h3 class="title">登录</h3>
            </div>
            <el-form-item prop="username" style="background: #fff">
              <span class="icon-item">
                <i class="icon iconfont icon-icon-user"></i>
              </span>
              <el-input
                v-model.trim="loginForm.username"
                placeholder="请输入用户名称"
                auto-complete="on"
                clearable
                ref="userInput"
                :maxlength="30"
              />
            </el-form-item>
            <el-form-item prop="password" style="background: #fff">
              <span class="icon-item">
                <i class="icon iconfont icon-mima54"></i>
              </span>
              <el-input
                v-model.trim="loginForm.password"
                placeholder="请输入登录密码"
                @keyup.enter.native="handleLogin"
                auto-complete="on"
                show-password
                clearable
                :maxlength="30"
                class="password-input"
              />
            </el-form-item>

            <el-button
              :loading="loading"
              type="warning"
              style="
                width: 100%;
                margin-top: 10px;
                margin-bottom: 30px;
                font-size: 16px;
              "
              @click.native.prevent="handleLogin"
              >登录</el-button
            >
          </el-form>
        </div>
      </el-main>
      <el-footer></el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      loginForm: {
        username: "",
        password: "",
      },
      loginRules: {
        username: [{ required: true, trigger: "blur", message: "请输入账号" }],
        password: [{ required: true, trigger: "blur", message: "请输入密码" }],
      },
      loading: false, //登录等待
      redirect: undefined,
      logo: "./favicon.ico",
      hiddenLogo: window.globalConfig.hiddenLogo,
      title: window.globalConfig.title,
      scene: "./static/img/scene.svg",
    };
  },
  watch: {
    $route: {
      handler: function (route) {
        this.redirect = route.query && route.query.redirect;
      },
      immediate: true,
    },
  },
  mounted() {
    // 自动获取焦点
    this.$nextTick(() => {
      this.$refs.userInput.focus();
    });
  },
  methods: {
    handleLogin() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$store
            .dispatch("Login", this.loginForm)
            .then(() => {
              this.loading = false;
              this.$router.push({ path: this.redirect || "/" });
            })
            .catch(() => {
              this.loading = false;
            });
        }
      });
    },
  },
};
</script>

